/* Main SCSS File */
/* Use for breakpoints */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
.tabs__card .tabs__card--time,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body {
  color: white; }

h1 {
  font-size: 40px;
  line-height: 1.2em;
  margin-bottom: 16px; }

h2 {
  font-size: 36px;
  line-height: 1.2em;
  margin-bottom: 16px; }

h3 {
  font-size: 32px;
  line-height: 1.2em;
  margin-bottom: 16px; }


h4,
.tabs__card .tabs__card--time {
  font-size: 28px;
  line-height: 1.2em;
  margin-bottom: 16px; }

h5 {
  font-size: 24px;
  line-height: 1.2em;
  margin-bottom: 16px; }

h6 {
  font-size: 20px;
  line-height: 1.2em;
  margin-bottom: 16px; }

h1,
h2,
h3 {
  font-family: 'Comfortaa', cursive;
  font-weight: 300; }


h4,
.tabs__card .tabs__card--time,
h5,
h6 {
  font-family: 'Archivo', sans-serif;
  font-weight: Normal; }

p {
  font-family: 'Archivo', sans-serif;
  font-size: 1em;
  line-height: 1.5em;
  margin-bottom: 16px; }

p::before {
  content: "";
  display: block;
  margin-bottom: 10px;
  width: 20%;
  border-bottom: 3px solid white; }

.overline, .signup > div > form label {
  font-family: 'Archivo', sans-serif;
  font-size: 12px;
  font-weight: 700; }

a {
  color: white;
  font-family: 'Archivo', sans-serif;
  text-decoration: none;
  transition: 0.2s; }

a:hover:not(.fa):not(.fa-facebook) {
  border-bottom: 3px solid #FF004A;
  font-family: 'Archivo', sans-serif; }

@media (min-width: 1100px) {
  h1 {
    font-size: 90px;
    line-height: 1.2em; }
  h2 {
    font-size: 76px;
    line-height: 1.2em; }
  h3 {
    font-size: 62px;
    line-height: 1.2em; }
  
  h4,
  .tabs__card .tabs__card--time {
    font-size: 48px;
    line-height: 1.2em; }
  h5 {
    font-size: 34px;
    line-height: 1.2em; }
  h6 {
    font-size: 20px;
    line-height: 1.2em; } }

[class^="button--"] {
  padding: 10px;
  width: 144px;
  color: white;
  font-family: 'Archivo', sans-serif;
  font-size: 0.875em;
  text-decoration: none;
  text-align: center;
  text-transform: capitalize;
  cursor: pointer; }

.button--pink {
  display: inline-block;
  background-color: #FF004A;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  transition-duration: 0.3s;
  transition-property: transform; }

.button--pink:hover,
.button--pink:active {
  transform: translateX(8px); }

.button--pink:focus {
  border: 2px solid orange; }

.button--submit {
  display: inline-block;
  width: 100%;
  margin: 0;
  border: 1px solid #FF004A;
  color: white;
  background: #FF004A;
  cursor: pointer; }

.grid__section {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 16px;
  padding: 4em 16px; }

.card-container__grid {
  grid-row: 2;
  display: grid;
  grid-template-columns: repeat(8, calc(50% - 40px));
  grid-template-rows: minmax(150px, 1fr);
  grid-gap: 16px;
  overflow-x: scroll;
  scroll-snap-type: x proximity; }

.wrapper {
  margin: auto;
  max-width: 1300px; }

.about {
  background-color: #171717;
  background-image: url("../images/about__bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: inherit;
  color: white; }
  .about .grid__item--about {
    grid-column: span 2; }
  .about p {
    grid-column: span 3;
    grid-row: 2; }

.headliners {
  padding: 4em 16px;
  background: #003941;
  scroll-snap-type: x mandatory; }

.card {
  grid-column: span 2;
  scroll-snap-align: center; }

.artist__title {
  background-color: #00AA6D;
  width: 100%;
  text-align: center;
  padding: 5px 0; }

.artist__image {
  width: 100%;
  padding-bottom: 16px; }

.p--fuschia {
  text-align: justify; }

.p--fuschia::before {
  border-bottom: 3px solid #FF004A;
  width: 10%; }

.schedule {
  background: linear-gradient(#003941, #00838E); }
  .schedule .grid__section p {
    grid-column: span 4;
    padding-bottom: 4.0625em; }
  .schedule .grid__section > h3 {
    grid-column: span 4;
    text-transform: uppercase;
    font-size: 1em; }
  .schedule .grid__section > h3::after {
    content: "";
    flex-direction: row;
    position: absolute;
    width: 50%;
    margin-left: 16px;
    border-bottom: 3px solid #00838E; }

/* Tabs for jQuery */
#tabs__day {
  display: flex;
  grid-column: span 4;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 16px; }
  #tabs__day > li > a {
    display: block;
    font-family: 'Comfortaa', cursive;
    font-size: 1.5em;
    text-decoration: none;
    color: white;
    cursor: pointer; }
  #tabs__day > li a.inactive {
    color: #999999; }
  #tabs__day > li > a:hover {
    color: white;
    border: 0; }
  #tabs__day > li > a:focus {
    background: #00AA6D;
    border-radius: 50%;
    padding: 10px; }

.tabs__container {
  grid-column: span 4; }

.tabs__card {
  display: flex;
  background-color: black;
  margin-bottom: 16px; }
  .tabs__card .tabs__card--time {
    margin: 0;
    padding: 0.5em;
    border-left: 2px solid #FF004A; }
  .tabs__card .tabs__info {
    border-left: 2px solid #FF004A;
    width: 100%; }
    .tabs__card .tabs__info .tabs__venue {
      display: block;
      font-family: 'archivo', sans-serif;
      font-size: 1em;
      background-color: #003941;
      margin: 0;
      padding: 2px 0 2px 16px; }
    .tabs__card .tabs__info .tabs__artist {
      margin: 0;
      padding: 5px 0 5px 16px;
      font-size: 1.602em; }

.signup {
  background-color: #ECECEC; }
  .signup > div > h2 {
    grid-column: span 3;
    color: #171717; }
  .signup > div > form {
    grid-row: 2;
    grid-column: span 4; }
    .signup > div > form div {
      display: flex;
      align-items: flex-end; }
    .signup > div > form label {
      color: #171717; }

#newsletter {
  border: 0;
  font-size: 1em;
  vertical-align: middle;
  background: none;
  border-bottom: 3px solid #FF004A; }

@media (min-width: 600px) {
  .grid__section {
    grid-template-columns: repeat(6, 1fr); }
  .about .grid__item--about {
    grid-column: 4/span 2; }
  .about p {
    grid-column: 4/span 2; }
  .card-container__grid {
    grid-template-columns: repeat(8, calc(27% - 50px)); }
  .schedule .grid__section p {
    grid-row: 2;
    grid-column: span 3; }
  .schedule .grid__section > h3 {
    grid-row: 3;
    grid-column: 2/span 2; }
  .schedule .grid__section > h3::after {
    width: 45%; }
  .schedule .grid__section #tabs__day {
    grid-row: 4;
    grid-column: span 6; }
  .schedule .grid__section .tabs__container {
    grid-row: 5;
    grid-column: span 6; }
  .signup div form {
    grid-column: span 6; }
  #newsletter {
    width: 100%; }
  .button--submit {
    width: 50%; } }

@media (min-width: 900px) {
  .grid__section {
    grid-template-columns: repeat(6, 1fr); }
  .about .grid__item--about {
    grid-column: 2/span 3;
    grid-row: 1; }
  .about p {
    grid-column: 4/span 2;
    grid-row: 1; }
  .card-container__grid {
    max-width: 1300px;
    margin: auto;
    grid-template-columns: repeat(8, calc(20% - 50px)); }
  .schedule .grid__section p {
    grid-row: 3;
    grid-column: span 2; }
  .schedule .grid__section > h3 {
    grid-row: 1;
    grid-column: 4/span 3; }
  .schedule .grid__section > h3::after {
    width: 30%; }
  .schedule .grid__section #tabs__day {
    grid-row: 2;
    grid-column: 4/ span 3;
    align-self: end; }
  .schedule .grid__section .tabs__container {
    grid-row: 3;
    grid-column: 4/span 3; } }

@media (min-width: 1100px) {
  .grid__section {
    grid-template-columns: repeat(12, 1fr); }
  .about {
    background-attachment: fixed; }
    .about .grid__item--about {
      grid-column: 1/span 6; }
    .about p {
      grid-column: 9/span 3; }
  .card-container__grid {
    max-width: 1300px;
    margin: auto;
    grid-template-columns: repeat(8, 1fr);
    overflow-x: unset; }
  .headliners {
    scroll-snap-type: none; }
  .card h3 {
    font-size: 1.5em; }
  .schedule .grid__section #tabs__day {
    grid-column: 5/ span 8; }
  .schedule .grid__section .tabs__container {
    grid-column: 5/span 8; }
  .tabs__card .tabs__card--time {
    font-size: 2em; }
  .signup div form {
    grid-column: 6 / span 7;
    grid-row: 2; }
  .signup div h2 {
    grid-column: span 9; } }

footer {
  background-color: #171717; }
  footer div > h6 {
    grid-column: span 4;
    margin-bottom: 16px; }
  footer div > h6:after {
    content: "";
    display: block;
    margin-top: 5px;
    width: 20%;
    border-bottom: 3px solid white; }

.social {
  grid-column: span 3; }

.social-media {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap; }

.fa {
  padding: 20px;
  text-decoration: none;
  border-radius: 50%;
  margin: 2px;
  text-align: center;
  border: 2px solid white;
  color: white;
  transition: 0.2s; }

.fa:hover,
.fa:active {
  border: 2px solid #00AA6D;
  color: #00AA6D; }

.fa-facebook {
  height: 60px;
  width: 60px; }

@media (min-width: 600px) {
  .social {
    grid-column: span 6; } }

@media (min-width: 900px) {
  footer div h6 {
    margin-bottom: 16px; }
  footer .grid__section .contact {
    grid-column: 2/span 2; }
  footer .grid__section .social {
    grid-column: span 3; } }

@media (min-width: 1100px) {
  footer .grid__section .social {
    grid-column: 6/span 6; } }

.navbar-menu {
  background-color: #fafafa;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1;
  transform: translateX(-100%);
  transition: 0.5s; }

.navbar-menu.active {
  transform: translateX(0);
  transition: 0.5s; }

.navbar-menu .menu-listing {
  padding: 0;
  margin: 0;
  text-align: left; }

.menu-listing li {
  display: block;
  margin: 16px; }

.menu-listing li a {
  color: #262626;
  display: block;
  font-size: 2rem;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  letter-spacing: 1px;
  text-decoration: none;
  transition: 0.5s; }

.menu-listing li a:active {
  color: #fff;
  transition: 0.5s; }

.menu {
  position: fixed;
  top: 0;
  right: 0;
  background-color: #00AA6D;
  height: 50px;
  width: 50px;
  cursor: pointer;
  transition: 0.3s;
  z-index: 9999; }

.menu span {
  position: absolute;
  height: 3px;
  width: 25px;
  background-color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s; }

.menu span:before {
  content: '';
  position: absolute;
  top: -8px;
  background-color: #fff;
  height: 3px;
  width: 25px;
  transition: 0.3s; }

.menu span:after {
  content: '';
  position: absolute;
  top: 8px;
  background-color: #fff;
  height: 3px;
  width: 25px;
  transition: 0.3s; }

.menu.active span {
  background-color: transparent; }

.menu.active span:before {
  background-color: white;
  transform: rotate(45deg);
  top: 0; }

.menu.active span:after {
  background-color: white;
  transform: rotate(-45deg);
  top: 0; }

.navbar-menu-md {
  display: none; }

header .grid__section .shape-group {
  display: none; }

.intro {
  background: linear-gradient(#003941, #00838E);
  color: white; }
  .intro .grid__item--intro {
    grid-column: 2 / span 2; }

@media (min-width: 600px) {
  header .grid__section .shape-group {
    /* apply this animation to the div containing the names*/
    display: block;
    grid-column: 1/span 3;
    grid-row: 1; }
    header .grid__section .shape-group .name-1 {
      opacity: 0;
      fill: white;
      animation: ckw 7s 1s infinite ease-in-out; }
    header .grid__section .shape-group .name-2 {
      fill: white;
      opacity: 0;
      animation: ckw 7s 3s infinite ease-in-out; }
    header .grid__section .shape-group .name-3 {
      opacity: 0;
      fill: white;
      animation: ckw 6s 2s infinite ease-in-out; }
    header .grid__section .shape-group .name-4 {
      opacity: 0;
      fill: white;
      animation: ckw 8s 4s infinite ease-in-out; }
  @keyframes ckw {
    0% {
      transform: translateY(-100%);
      opacity: 0; }
    50% {
      transform: translateY(0);
      opacity: 0.5; }
    100% {
      transform: translateY(100%);
      opacity: 0; } }
  .intro .grid__item--intro {
    grid-row: 1;
    grid-column: 4/span 3; } }

@media (min-width: 900px) {
  .navbar-menu-md {
    display: inline-block;
    position: absolute;
    width: 100%; }
    .navbar-menu-md .menu-listing li a {
      font-size: 1em;
      color: white; }
    .navbar-menu-md .menu-listing li {
      display: inline-block;
      background-color: transparent; }
  .menu {
    display: none; }
  header .grid__section .shape-group {
    grid-column: 1/span 3; }
  .intro .grid__item--intro {
    grid-column: 4/span 2; } }

@media (min-width: 1100px) {
  header .grid__section .shape-group {
    grid-column: span 6; }
  .intro .grid__item--intro {
    grid-column: 8/span 4; }
    .intro .grid__item--intro p {
      max-width: 400px; } }
